import { useTranslation } from "gatsby-plugin-react-i18next";

import Card from "react-bootstrap/Card";

import { EditionData } from "../../data";
import { workshopCards, inviteCards } from "../general/workshopCards";
import useEventDates from "../../hooks/useEventDates";

import * as styles from "./workshops.module.css";

type WorkshopCardProps = {
  card: any;
  date: any;
};

const WorkshopCard: React.FC<WorkshopCardProps> = ({ card, date }) => {
  const { t } = useTranslation();
  const descriptions = t("descriptions", { returnObjects: true });

  return (
    <Card className={`card ${styles.card}`}>
      <Card.Header className={"textMuted cardHeader"}>
        {date + card.date} {t("months.march")}, {card.time}
      </Card.Header>

      <Card.Body className={"cardBody"}>
        <Card.Title className={"cardTitle"}>{card.title}</Card.Title>
        <Card.Subtitle className={"text-muted cardSubtitle"}>
          {card.speakers}
        </Card.Subtitle>
        {/*Show only if there is a description */}
        {card.id != null && <Card.Text>{descriptions[card.id]}</Card.Text>}
      </Card.Body>

      <Card.Footer className={`text-muted ${styles.footer}`}>
        {card.link !== "#" && (
          <Card.Link className={"a textMuted cardLink"} href={card.link}>
            {card.link}
          </Card.Link>
        )}
        {card.link === "#" && "N/A"}
      </Card.Footer>
    </Card>
  );
};

type WorkshopsProps = {
  editionData: EditionData;
};

const Workshops: React.FC<WorkshopsProps> = ({ editionData }) => {
  const { t } = useTranslation();

  const { workshops: date } = useEventDates(editionData);
  const details = t("details", { returnObjects: true }) as string[];
  const edition = editionData.edition;

  return (
    <div className={`page ${styles.workshops}`}>
      <h2 className={"date"}>{date}</h2>
      <h1 className={"title"}>{t("title")}</h1>
      {/* Details about the workshops */}
      <ul className={`ul ${styles.details}`}>
        {details.map((item, index) => (
          <li key={"li workshopDetails" + index} className={"li"}>
            {item}
          </li>
        ))}

        <p>
          {t("github")}
          <a
            className={"a cardLink"}
            target={"_blank"}
            rel={"noreferrer"}
            href={"https://github.com/Nitro-Language-Processing/Workshops"}
          >
            https://github.com/Nitro-Language-Processing/Workshops
          </a>
        </p>
      </ul>

      <ul className={`ul ${styles.cards}`}>
        {workshopCards[edition].map((item) => (
          <WorkshopCard
            key={"workshopCard" + edition + item.id}
            card={item}
            date={parseInt(date)}
          />
        ))}
      </ul>

      {/*Show only if there are special invites */}
      {inviteCards[edition].length > 0 && (
        <h1 className={"title"}>{t("inviteTitle")}</h1>
      )}

      {inviteCards[edition].length > 0 && (
        <ul className={`ul ${styles.cards}`}>
          {inviteCards[edition].map((item, index) => (
            <WorkshopCard
              key={"inviteCard" + edition + index}
              card={item}
              date={parseInt(date)}
            />
          ))}
        </ul>
      )}
    </div>
  );
};

export default Workshops;
